<template>
  <div>
    <div>加载: {{process}} %</div>
    <button @click="play" :class={active:isReadyPlay}>播放</button>
  </div>
</template>

<script>
export default {
  props: {
    process: {
      type: Number,
      default: 0
    },
    isReadyPlay: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    play() {
      if (this.isReadyPlay === false) {
        alert('没有加载完...')
        return
      }
      let ret = confirm("确定播放?")
      if (ret) {
        this.$emit('confirm')
      }
    }
  }
}
</script>

<style>
 .active {
    background-color: #007acc;
  }
</style>



